}

.accordions .accordion > button {
  background: #DDD;
  color: #191919;
  cursor: pointer;
  font-size: 1em;
  margin-top: .5em;
  padding: .5em 1em;
  position: relative;
  text-align: left;
  width: 100%;
}

.accordions .accordion > button:hover {
  background: #d0d0d0;
}

.accordions .accordion > button::after {
  bottom: 0;
  height: 1em;
  margin: auto 0;
  position: absolute;
  right: 1em;
  top: 0;
  width: 1em;
}

.accordions .accordion > button.open {
  background: #d0d0d0;
}

.accordions .accordion > button.open::after {
  font: normal normal normal 1em / 1 FontAwesome;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  speak: none;
  text-rendering: auto;
  text-transform: none;
  content: "";
}

.accordions .accordion > button.close::after {
  font: normal normal normal 1em / 1 FontAwesome;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  speak: none;
  text-rendering: auto;
  text-transform: none;
  content: "";
}

.accordions .accordion > div {
  padding: 1em 0;
}

.accordions .accordion > div.hidden {
  display: none;
}

/* = Media Queries = */
@media only screen and (min-width: 669px) {
  /* = Layout = */
  aside.primary {
    float: right;
    margin-bottom: 0;
    width: 20%;
  }
  aside.primary nav ul {
    text-align: left;
  }
  aside.primary ~ main {
    float: left;
    padding-right: 2rem;
    width: 80%;
  }
}

@media only screen and (min-width: 1024px) {
  /* = Layout = */
  .branding {
    float: left;
    margin-bottom: 0;
    margin-top: 2rem;
  }
  .wrapper {
    float: right;
    width: 80%;
  }
  nav ul {
    text-align: left;
  }
  nav.primary {
    float: left;
    margin-bottom: 0;
    margin-top: 2.2rem;
    width: 65%;
  }
  nav.primary > ul > li {
    font-size: 1.3rem;
    margin-left: 1rem;
    position: relative;
  }
  nav.primary > ul > li:first-child {
    margin-left: 0;
  }
  nav.primary ul {
    text-align: left;
  }
  nav.primary ul li li {
    display: block;
    float: none;
  }
  nav.primary ul .dropdown {
    background-color: #191919;
    left: 0;
    min-width: 180px;
    position: absolute;
    z-index: 2;
  }
  nav.primary ul li:hover .dropdown {
    display: block;
  }
  nav.primary ul .dropdown li {
    font-size: 0.9rem;
  }
  nav.primary ul .dropdown li a {
    color: #FFF;
    padding: 0.3rem 0.8rem;
  }
  nav.primary ul .dropdown li a:hover {
    background-color: #333333;
  }
  .utilities {
    float: right;
    width: 30%;
  }
  .utilities nav.utility {
    margin-bottom: 0.4rem;
  }
  .utilities nav.utility ul {
    text-align: right;
  }
  aside.primary {
    width: 25%;
  }
  aside.primary ~ main {
    width: 75%;
  }
  /* = Typography = */
  h1 {
    font-size: 2.6rem;
  }
  h2 {
    font-size: 2.1rem;
  }
  h3 {
    font-size: 1.6rem;
  }
  h4 {
    font-size: 1.25rem;
  }
  h5 {
    font-size: 1.15rem;
  }
}

/* = Print Styles = */
@media print {
  * {
    background-color: transparent !important;
    box-shadow: none !important;
    color: #000 !important;
    /* Black prints faster: h5bp.com/s */
    text-shadow: none !important;
  }
  a {
    text-decoration: underline;
  }
  pre,
  blockquote,
  tr,
  img,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    page-break-inside: avoid;
  }
  thead {
    display: table-header-group;
  }
  @page {
    margin: 0.5cm;
  }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }
}
